.nav-tabs {
  width: 100%;
  margin: 0;
  list-style-type: none;
  border-bottom: 1px solid #ddd;
  display: flex;


  li {
    margin-bottom: -1px;

    a {
      display: block;
      text-decoration: none;
      margin-right: 2px;
      line-height: 1.5;
      padding: 10px;
      border: 1px solid transparent;
      border-radius: 4px 4px 0 0;

      &:hover {
        border-color: #eee #eee #ddd #ddd;
      }
    }

    &.active {
      a, a:hover, a:focus {
        color: #555;
        cursor: default;
        background-color: #fff;
        border: 1px solid #ddd;
        border-bottom-color: transparent;
      }
    }
  }

}

.tab-content {
  .tab-pane {
    width: 100%;
    display: none;

    &.active {
      display: block;
      padding: 10px;
      background-color: #fff;
    }
  }
}

